body {
    overflow: hidden;
}

input[type=checkbox] {
    position: absolute;
    z-index: -1000;
    left: -1000px;
    overflow: hidden;
    clip: rect(0 0 0 0);
    height: 1px;
    width: 1px;
    margin: -1px;
    padding: 0;
    border: 0;
}

input[type=checkbox] + label.checkbox-label {
    padding-left: 27px;
    height: 22px;
    display: inline-block;
    line-height: 22px;
    background-repeat: no-repeat;
    background-position: 0 0;
    font-size: 20px;
    vertical-align: middle;
    cursor: pointer;
}

input[type=checkbox] + label.checkbox-label.small-label {
    font-size: 11px;
    line-height: 11px;
}

input[type=checkbox]:checked + label.checkbox-label {
    background-position: 0 -22px;
}

label.checkbox-label {
    background-image: url(../images/checkbox.png);
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.daterange-wrp .noUi-tooltip {
    width: 100px;
    left: -37px;
}

label {
    font-weight: 200;
    font-size: 18px;
    line-height: 22px;
}

label.small-label {
    font-size: 11px;
    line-height: 11px;
}

.filter-wrp {
    margin-top: 10px;
}

.filters-wrp {
    width: 25%;
    position: absolute;
    top: 0%;
    bottom: 0%;
    overflow-y: auto;
    background: #F9F9F9;
    border-right: 1px solid #EEE;
    padding: 5px 15px;
    left: 0px;
}

.results-wrp {
    left: 26%;
    right: 0%;
    top: 0%;
    bottom: 0%;
    padding: 5px 25px;
    position: absolute;
    overflow-y: auto;
}

button {
    background: #fff;
}

.view-buttons-wrp {
    position: fixed;
    top: 0px;
    padding: 5px;
    left: 28%;
    background: #fff;
    overflow: hidden;
    z-index: 2;
}

.results-visualization-wrp {
    margin-top: 50px;
}

.card table.values td, .card table.summary-table td {
    text-transform: capitalize;
}

.range-input {
    -webkit-appearance: none;
    background-color: red;
    width: 100%;
    height: 2px;
}

.range-input:focus {
    outline: none;
}

.range-input-wrp {
    width: 90%;
    margin: 5px auto;
}

input[type=range]::-webkit-slider-thumb {
	appearance: none;
	-webkit-appearance: none;
	-moz-appearance: none;
	background-color: #fff;
	border: 1px solid #333;
	width: 14px;
	height: 14px;
	border-radius: 15px;
}